 HEADER
-----------------------*/

.header {
	background: url(/Portfolio-Website/static/media/header.86d9973a.jpg);
	background-size: cover;

	background-attachment: fixed;
	background-repeat: no-repeat;
	color: #cfcfcf;
}

.header .overlay {
	/* FF3.6+ */
	/* Chrome,Safari4+ */
	/* Chrome10+,Safari5.1+ */
	/* Opera 11.10+ */
	/* IE10+ */
	background: -webkit-gradient(linear,
			left top, left bottom,
			from(rgba(0, 0, 0, 0.8)),
			color-stop(17%, rgba(0, 0, 0, 0.73)),
			color-stop(35%, rgba(0, 0, 0, 0.66)),
			color-stop(62%, rgba(0, 0, 0, 0.55)),
			to(rgba(0, 0, 0, 0.4)));
	background: linear-gradient(to bottom,
			rgba(0, 0, 0, 0.8) 0%,
			rgba(0, 0, 0, 0.73) 17%,
			rgba(0, 0, 0, 0.66) 35%,
			rgba(0, 0, 0, 0.55) 62%,
			rgba(0, 0, 0, 0.4) 100%);
	/* W3C */
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#cc000000',
			endColorstr='#66000000',
			GradientType=0);
	/* IE6-9 */
	height: 100vh;
	background-attachment: fixed;
}

.header-content {
	padding-top: 35vh;
	padding-left: 10vh;
}

.header-content h4 {
	font-size: 18px;
	font-weight: 400;
	line-height: 60px;
	margin: 0px;
	color: #fff;
	letter-spacing: 2px;
	text-transform: uppercase;
}

.header-content h2 {
	color: #fff;
	font-size: 60px;
	font-weight: 00;
	letter-spacing: 10px;
	margin: 0;
	text-transform: uppercase;
	margin-bottom: 50px;
}

.header-content h2 span {
	color: #1cbac8;
	text-shadow: 2px 2px 0 rgba(0, 0, 0, .75);
}

.header_btn,
.header_btn:focus,
.header_btn:active,
.header_btn:visited {
	color: #fff;
	font-size: 12px;
	font-weight: 600;
	border: 1px solid #fff;
	border-radius: 0px;
	padding: 12px 40px;
	text-transform: uppercase;
	letter-spacing: 1px;
	margin-right: 10px;
	background-color: transparent;
	-webkit-transition: all .5s;
	transition: all .5s;
	width: 185px;
}

.header_btn:hover {
	color: #fff;
	background-color: #1cbac8;
	border-color: #1cbac8;
	-webkit-transition: all .5s;
	transition: all .5s;
}

@media (max-width: 768px) {
	.header-content {
		padding-top: 20vh;
		padding-left: 4vh;
	}
	.header-content h4 {
		font-size: 15px;
		line-height: 40px;
		font-weight: 200;		
	}
	.header-content h2 {
		font-size: 35px;
		margin-bottom: 35px;
		margin-top: 30px;
	}
	.header-content h2 span {
		display: block;
		line-height: 35px;
		height: 50px;
	}	
	.header-content h4 span {
		display: block;
		line-height: 35px;
	}
	.header_btn {
		padding-top: 10px;
		padding-bottom: 10px;
		margin-top: 10px;
	}

}

